import { Component, Input } from "@angular/core";
import { EmailType } from "src/app/shared/models/types/email-type.model";

@Component({
  selector: "app-email-type-i18n",
  template: `
    <ng-container [ngSwitch]="key">
      <ng-container i18n *ngSwitchCase="type.Private">Private</ng-container>
      <ng-container i18n *ngSwitchCase="type.Work">Work</ng-container>
      <ng-container i18n *ngSwitchCase="type.Public">Public</ng-container>
      <ng-container i18n *ngSwitchDefault>Not Defined</ng-container>
    </ng-container>
  `,
})
export class EmailTypeI18nComponent {
  type = EmailType;

  @Input()
  key!: EmailType;
}
