import { Component, Input } from "@angular/core";
import { PhoneType } from "src/app/shared/models/types/phone-type.model";

@Component({
  selector: "app-phone-type-i18n",
  template: `
    <ng-container [ngSwitch]="key">
      <ng-container i18n *ngSwitchCase="type.Home">Home</ng-container>
      <ng-container i18n *ngSwitchCase="type.Work">Work</ng-container>
      <ng-container i18n *ngSwitchCase="type.Mobile">Mobile</ng-container>
      <ng-container i18n *ngSwitchDefault>Not Defined</ng-container>
    </ng-container>
  `,
})
export class PhoneTypeI18nComponent {
  type = PhoneType;

  @Input()
  key!: PhoneType;
}
